import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import { Search } from 'react-feather';

class WardsModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Wards
                        </Modal.Title>
                    </Modal.Header>

                    <Form onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                                <Form.Group controlId="WardName">
                                    <Form.Label>Ward Name</Form.Label>
                                    <Form.Control type="text"
                                    name="WardName"
                                    placeholder="Enter Ward Name"
                                    value={this.props.WardName}
                                    onChange={this.props.changeEventandValidation} />
                                    <div className="invalid-feedback d-block">
                                        {this.props.WardNameError}
                                    </div>
                                </Form.Group>
                                <Form.Group controlId="SubCountyType">
                                    <Form.Label>Sub County Type</Form.Label>
                                    <InputGroup>
                                    <Form.Control value={this.props.SubCountyTypeID} onChange={this.props.handleChangeSubCountyTypeID} hidden name="SubCountyID" />
                                        <Form.Control
                                            type="text"
                                            name="SubCountyType"
                                            value={this.props.SubCountyTypeDescription}
                                            onChange={this.props.changeEventandValidation}
                                            placeholder="Search Sub County Type"
                                            aria-describedby="inputGroupPrepend"
                                            readOnly
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSubCountyTypeSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                    </InputGroup>
                                    <div className="invalid-feedback d-block">
                                        {this.props.SubCountyTypeError}
                                    </div>
                                </Form.Group>
                                <Form.Group controlId="SystemStatusType">
                                    <Form.Label>System Status Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control value={this.props.SystemStatusTypeID} onChange={this.props.handleChangeSystemStatusTypeID} hidden name="SystemStatusID" />
                                        <Form.Control
                                            type="text"
                                            name="SystemStatus"
                                            value={this.props.SystemStatusTypeDescription}
                                            onChange={this.props.changeEventandValidation}
                                            placeholder="Search System Status Type"
                                            aria-describedby="inputGroupPrepend"
                                            readOnly
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowStatusSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                    </InputGroup>
                                    <div className="invalid-feedback d-block">
                                        {this.props.SystemStatusTypeError}
                                    </div>
                                </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Ward
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description_del">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.WardName} onChange={this.props.changeWardName} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default WardsModal;