import React from 'react';
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import SubCountyTypeSearch from '../../SearchComponents/SubCountyTypeSearch/SubCountyTypeSearch';
import WardsTable from "./Tables/WardsTable";
import WardsModal from "./Modals/WardsModal"


class Wards extends React.Component {
    constructor(props, context) {
        super(props, context);
        var DropdownvalidationSystemStatus = 0;
        var DropdownvalidationSubCounty = 0;
        this.state = {
            errors: {},
            columns: [],
            data: [],
            filteredData: [],
            filteredSubCountyTypeData: [],
            filteredStatusData: [],
            value: '',
            show: false,
            showdelete: false,
            WARD_ID: 0,
            WARD_NAME: '',
            SET_SUBCOUNTY_ID: 0,
            SYS_STATUS_ID: 0,
            CREATEDBY: '',
            MODIFIEDBY: '',
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            opensubcountytypesearch: false,
            openstatussearch: false,

            SUBCOUNTYYPEDESCRIPTION: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',

            searchcolumns: [],

            searchdata: [],
            searchsubcountytypedata: [],
            searchstatusdata: [],

            searchFilteredSubCountyTypeData: [],
            searchFilteredStatusData: [],

            searchsubcountytypevalue: '',
            searchstatusvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSubCountyTypeSearch = () => {
            this.setState({ opensubcountytypesearch: true });
        };

        this.handleShowStatusSearch = () => {
            this.setState({ openstatussearch: true });
        };

        this.handleHideSubCountyTypeSearch = () => {
            this.setState({ opensubcountytypesearch: false });
        };

        this.handleHideStatusSearch = () => {
            this.setState({ openstatussearch: false });
        };

        this.populateData();
        this.populateTypes();
        this.populateStatus();

    }

    handleValidation(){
        let errors = {};
        let formIsValid = true;

        if(!this.state.WARD_NAME){
            formIsValid = false;
            errors["WardName"] = "Please Enter Ward Name";
        }

        if(!this.state.SUBCOUNTYYPEDESCRIPTION){
            formIsValid = false;
            errors["SubCountyType"] = "Please select Sub County";
        }

        if(!this.state.SUBCOUNTY_ID)
        {
         formIsValid = false;
         errors["SubCountyType"] = "Please select Sub County";
        }

        if(!this.state.SYSTEMSTATUSTYPEDESCRIPTION){
            formIsValid = false;
            errors["SystemStatus"] = "Please select System Status Type";
        }

        if(!this.state.SYS_STATUS_ID)
        {
         formIsValid = false;
         errors["SystemStatus"] = "Please select System Status Type";
        }

        if(this.DropdownvalidationSubCounty === 1)
        {
            errors["SubCountyType"] = "";
            this.DropdownvalidationSubCounty = 0;
        }

        if(this.DropdownvalidationSystemStatus === 1)
        {
            errors["SystemStatus"] = "";
            this.DropdownvalidationSystemStatus = 0;
        }

        this.setState({errors: errors});
        return formIsValid;
    }

    populateData() {
        const getdata = `
            query{
                Wards{
                    WARD_ID
                    WARD_NAME
                    SET_SUBCOUNTY_ID   
                    SUBCOUNTY_TYPE{
                        SUBCOUNTY_ID
                        SUBCOUNTY_NAME
                    }
                    SYS_STATUS_ID
                    SYS_STATUS_TYPE{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    CREATEDBY
                    MODIFIEDBY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.Wards
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Wards Data');
                }
            })

    }

    populateTypes() {
        debugger;
        const getdata = `
            query {
                SubCounties {
                    SUBCOUNTY_ID,
                    SUBCOUNTY_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchsubcountytypedata: data.data.SubCounties
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to SubCounties Load SubCounty Data');
                }
            })
    }

    populateStatus() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchstatusdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        debugger;

        if(this.handleValidation()){

            const form = `
            mutation{
                addWards(
                    WARD_NAME:"${this.state.WARD_NAME}",
                    SET_SUBCOUNTY_ID:${this.state.SUBCOUNTY_ID},
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.MODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        WARD_ID
                }
            }
            `;

            const formEdit = `
            mutation{
                updateWards(
                    WARD_ID:${this.state.WARD_ID},
                    WARD_NAME:"${this.state.WARD_NAME}",
                    SET_SUBCOUNTY_ID:${this.state.SUBCOUNTY_ID},
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.MODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        WARD_ID
                }
            }
            `;
        
            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteWards(
                WARD_ID:${this.state.WARD_ID}
                ){
                    WARD_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSubCountyTypeSearch = (e) => {

        this.setState({
            SUBCOUNTY_ID: e.SUBCOUNTY_ID,
            SUBCOUNTYYPEDESCRIPTION: e.SUBCOUNTY_NAME,
            opensubcountytypesearch: false,
        });

        this.DropdownvalidationSubCounty=1;
        this.DropdownvalidationSystemStatus=1;
        this.handleValidation();
    };

    handleCloseStatusSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            openstatussearch: false,
        });

        this.DropdownvalidationSystemStatus=1;
        this.DropdownvalidationSubCounty=1;
        this.handleValidation();
    };

    handleEditData = (e) => {
        this.setState({
            WARD_ID: e.WARD_ID,
            WARD_NAME: e.WARD_NAME,
            SET_SUBCOUNTY_ID: e.SET_SUBCOUNTY_ID,
            SUBCOUNTYYPEDESCRIPTION: e.SUBCOUNTY_TYPE.SUBCOUNTY_NAME,
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_TYPE.SYS_STATUS_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            WARD_ID: e.WARD_ID,
            WARD_NAME: e.WARD_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            WARD_NAME: '',
            SET_SUBCOUNTY_ID: '',
            SUBCOUNTYYPEDESCRIPTION: '',
            SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_TYPE.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.WARD_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SUBCOUNTY_TYPE.SUBCOUNTY_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_TYPE.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.WARD_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.SUBCOUNTY_TYPE.SUBCOUNTY_NAME.toString().toLowerCase().includes(value.toLowerCase())
                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchSubCountyTypeFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchsubcountytypedata
        let filteredSubCountyTypeData = this.state.searchFilteredSubCountyTypeData
        this.setState({ searchsubcountytypevalue: value });

        if (value.length) {
            filteredSubCountyTypeData = data.filter(item => {
                let startsWithCondition =
                    item.SUBCOUNTY_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SUBCOUNTY_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredSubCountyTypeData })
        }
    }

    handleSearchStatusFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchstatusdata
        let filteredStatusData = this.state.searchFilteredStatusData
        this.setState({ searchstatusvalue: value });

        if (value.length) {
            filteredStatusData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredStatusData })
        }
    }

    changeEventandValidation = (e) => {
      
        if(e.target.name==="WardName")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            WARD_NAME:e.target.value
            });
        }

        if(e.target.name==="SubCountyType")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            SUBCOUNTYYPEDESCRIPTION:e.target.value
            });
        }

        if(e.target.name==="SystemStatus")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            SYSTEMSTATUSTYPEDESCRIPTION:e.target.value
            });
        }

        if(e.target.name==="SubCountyID")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            SUBCOUNTY_ID:e.target.value
            });
        }

        if(e.target.name==="SystemStatusID")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            SYS_STATUS_ID:e.target.value
            });
        }        
         
    this.handleValidation();
   
};


    render() {

        const { data, columns, value, filteredData, filteredSubCountyTypeData, filteredStatusData, searchsubcountytypevalue, searchstatusvalue, searchstatusdata, searchsubcountytypedata } = this.state

        return (
            <>
                <Helmet><title>Wards</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Wards"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Wards</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <SubCountyTypeSearch
                                        handleSearchSubCounty={this.handleCloseSubCountyTypeSearch}
                                        opensearch={this.state.opensubcountytypesearch}
                                        handleHideSearch={this.handleHideSubCountyTypeSearch}
                                        value={searchsubcountytypevalue}
                                        data={searchsubcountytypedata}
                                        handleFilter={this.handleSearchSubCountyTypeFilter}
                                        filteredData={filteredSubCountyTypeData}
                                    />

                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseStatusSearch}
                                        opensearch={this.state.openstatussearch}
                                        handleHideSearch={this.handleHideStatusSearch}
                                        value={searchstatusvalue}
                                        data={searchstatusdata}
                                        handleFilter={this.handleSearchStatusFilter}
                                        filteredData={filteredStatusData}
                                    />

                                    <WardsTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <WardsModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSubCountyTypeSearch={this.handleShowSubCountyTypeSearch}
                                        handleShowStatusSearch={this.handleShowStatusSearch}

                                        SubCountyTypeID={this.state.SET_SUBCOUNTY_ID}
                                        handleChangeSubCountyTypeID={e => this.setState({ SET_SUBCOUNTY_ID: e.target.value })}

                                        SubCountyTypeDescription={this.state.SUBCOUNTYYPEDESCRIPTION}
                                        handleChangeSubCountyTypeDescription={e => this.setState({ SUBCOUNTYYPEDESCRIPTION: e.target.value })}

                                        WardName={this.state.WARD_NAME}
                                        changeWardName={e => this.setState({ WARD_NAME: e.target.value })}

                                        SystemStatusTypeID={this.state.SYS_STATUS_ID}
                                        handleChangeSystemStatusTypeID={e => this.setState({ SYS_STATUS_ID: e.target.value })}

                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        handleChangeSystemStatusTypeDescription={e => this.setState({ SYSTEMSTATUSTYPEDESCRIPTION: e.target.value })}

                                        changeEventandValidation={this.changeEventandValidation}
                                        WardNameError={this.state.errors["WardName"]}
                                        SubCountyTypeError={this.state.errors["SubCountyType"]}
                                        SystemStatusTypeError={this.state.errors["SystemStatus"]}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(Wards));
